*::-webkit-scrollbar {
  display: none;
  overflow: overlay;
}
.App {
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #282c34;
  width: 100%;
  min-height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.loader{
    position: fixed;
    left: 50%;
    top: 35%;
    z-index: 1000;
    height: 31px;
    width: 31px;
}