.body {
  padding: 30px;
  width: 100%;
  height: 100vh;
  overflow-y: overlay;
  flex: 0.8;
  color: white;
  background: linear-gradient(transparent, rgba(0, 0, 0, 1));
  background-color: rgb(91, 87, 115);
  }

  .body::-webkit-scrollbar {
    display: none;
  }

  .card__holder{
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: flex;
    overflow-x: overlay;
    flex-grow: 1;
    
  }

  .menu__card{
    display: flex;
    align-items: flex-start;
    padding: 10px;
  }

  .root {
    display: 'flex';
    flexWrap: 'wrap';
    justifyContent: 'space-around';
    overflow: 'hidden';

};
.gridList {
    width: 100px;
    height: 450px;
    min-height: 450px;
};
.icon {
    color: 'rgba(255, 255, 255, 0.54)';
}